/* reset css */
* {
	box-sizing: border-box;
}

* {
	-webkit-text-size-adjust: none
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	outline: 0;
	font-size: 100%;
	vertical-align: baseline;
	background: transparent;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

ul {}

blockquote,
q {
	quotes: none;
}

html {
	height: 100%;
	background: $white;
	-webkit-text-size-adjust: none;
	-ms-text-size-adjust: 100%;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	scroll-behavior: smooth;

}

img {
	max-width: 100%;
	height: auto;
	vertical-align: top;
}

body {
	font-family: $main-font;
	font-size: $font-size;
	line-height: $line-height;
	font-weight: $font-weight;
	color: $txt-color;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	display: flex;
	min-height: 100%;
}

input,
textarea,
select {
	vertical-align: middle;
	font-family: $main-font;
	font-size: $font-size;
	line-height: $line-height;
	font-weight: $font-weight;
	color: $txt-color;
}

button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: none;
	cursor: pointer;
	transition: .3s all;
}

button::-moz-focus-inner,
input[type="reset"]::-moz-focus-inner,
input[type="button"]::-moz-focus-inner,
input[type="submit"]::-moz-focus-inner {
	border: none;
	padding: 0;
}

a,
button,
input,
textarea,
select {
	&:focus {
		outline: none;
	}
}

input[type="search"] {
	-webkit-appearance: none;
}

input {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[type="text"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[type="email"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

input[type="password"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
}

table {
	border-collapse: collapse;
}

*,
*:before,
*:after {
	box-sizing: border-box;
}

p {
	margin: 0 0 16px;
}

/*typography*/
h1,
.h1 {
	font-size: 60px;
	line-height: 1.25;
	font-family: $main-font;
	font-weight: 700;
	color: $txt-color;
	text-align: center;
	margin: 0 0 15px;

	@include max-screen(768px) {}
}

h2,
.h2 {
	font-size: 56px;
	line-height: 1.25;
	font-family: $main-font;
	font-weight: 400;
	color: $txt-color;
	text-align: center;
	margin: 0 0 15px;

	@include max-screen(768px) {}
}

h3,
.h3 {
	font-size: 52px;
	line-height: 1.25;
	font-family: $main-font;
	font-weight: 400;
	color: $txt-color;
	text-align: center;
	margin: 0 0 15px;

	@include max-screen(768px) {}
}

h4,
.h4 {
	font-size: 48px;
	line-height: 1.25;
	font-family: $main-font;
	font-weight: 400;
	color: $txt-color;
	text-align: left;
	margin: 0 0 15px;

	@include max-screen(768px) {}
}